
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  width: 1000px;
  margin: 0 auto;
  background-color: #fff;
}

.con-nav .main-nav {
  margin-top: 50px;
  margin-bottom: 16px;
  background-color: #538dd5;
  text-align: left;
  position: relative;
  height: 40px;
}
.con-nav .main-nav ul.ul-lev1 {
  padding-left: 10px;
  margin-left: 150px;
  font-size: 15px;
  display: inline-block;
}
.con-nav .main-nav ul.ul-lev1 li.li-lev1 {
  float: left;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  cursor: pointer;
  position: relative;
  color: #fff;
}
.con-nav .main-nav ul.ul-lev1 li.li-lev1:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.con-nav .main-nav ul.ul-lev1 li.li-lev1:after {
  content: '';
  display: none;
  width: 100%;
  height: 1px;
  background-color: #fff;
  position: absolute;
  bottom: 1px;
  left: 0px;
}
.con-nav .main-nav ul.ul-lev1 li.li-lev1.active {
  color: #fff;
  background-color: rgba(0, 0, 0, 0.22);
}
.con-nav .main-nav div.control {
  position: absolute;
  right: 12px;
  top: -38px;
  color: #2992e0;
  font-size: 14px;
}
.con-nav .main-nav div.control div.login-status {
  float: left;
  display: inline-block;
  height: 40px;
  line-height: 40px;
  margin-right: 30px;
}
.con-nav .main-nav div.control div.login-status .user-control span {
  color: #3296fa;
  padding: 3px;
}
.con-nav .main-nav div.control div.language {
  float: left;
  display: inline-block;
}
.con-nav .clear-float:after {
  content: '';
  height: 0;
  line-height: 0;
  display: block;
  visibility: hidden;
  clear: both;
}

.foot{
    background-color: rgb(211, 220, 230);
    line-height: 60px;
    position: relative;
    width: 100%;
    bottom: 0;
    left: 0;
}

.myaccount_setting {
  position: relative;
  margin-top: 20px;
  min-height: 530px;
}
.myaccount_setting .aside {
  position: absolute;
  left: 0;
  top: 0;
  width: 220px;
  min-height: 100px;
}
.myaccount_setting .main-box {
  width: 90%;
  padding-left: 310px;
  padding-right: 180px;
}

.wrap .el-submenu {
  text-align: left;
}
.wrap .el-menu-item {
  text-align: left;
}
